#menu1{
  visibility: hidden;
  transition: visibility .5s, max-height .5s;
  max-height: 0;
  overflow: hidden;
}

#menu1.open {
	visibility: visible;
	max-height: 120px;
}

#icon1.rotate{
    animation: rotations .5s forwards;
}

#icon1.rotate180{
	animation: rotation .5s forwards;
}

.active{
	background-color: rgb(55 65 81);
	padding : .5em;
	border-radius: 5px;
	color: white;
	transition: all .5s ease;
}

@media screen and (min-width: 768px) { 
	.active{ width: 13rem; } 
}

.navlinks{
	transition: all .5s ease;
}

.navlinks:hover:not(.active) {
	color: white;
	border-radius: 5px;
	background-color: rgb(31 41 55);
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(180deg);
  }
}

@keyframes rotations {
  from {
    transform: rotate(180deg);
  }
  to {
    transform: rotate(360deg);
  }
}